
import React, { useState } from 'react'
import { useFirebase } from '../../firebase/firebaseAuth';
import './loginpage.css'

const Loginpage = () => {
    const {googleSignIn, loginUser} = useFirebase();
   const [email, setEmail] = useState('');
   const [password, setPassword] = useState('');
   const handleSubmit = (e) =>{
       e.preventDefault();
    loginUser(email, password);
    setEmail('')
    setPassword('')
   }

    return (
        <div className='loginpage'>
            <h2>Login Here</h2>
            <form onSubmit={handleSubmit}>
            <div className="form-floating mb-3">
  <input type="email" className="form-control"  placeholder="name@example.com" onChange={e=> setEmail(e.target.value)} value={email}/>
  <label for="floatingInput">Email address</label>
</div>
<div className="form-floating">
  <input type="password" className="form-control mb-3"  placeholder="Password" onChange={e=> setPassword(e.target.value)} value={password}/>
  <label for="floatingPassword">Password</label>
</div>
<button className='btn  btn-outline-primary'>Submit</button>
            </form>
            <button className='btn  btn-outline-secondary mb-3' onClick={googleSignIn}>Google Login</button>
        </div>
    )
}

export default Loginpage
