import React, { useState } from 'react'
import { useFirebase } from '../../firebase/firebaseAuth';
import './register.css'

const RegisterPage = () => {
    const {registerWithEmail, googleSignIn, googleRedirect} = useFirebase();
   const [name, setName] = useState('');
   const [email, setEmail] = useState('');
   const [password, setPassword] = useState('');
   const [conPassword, setConpassword] = useState('');
   const handleSubmit = (e) =>{
    e.preventDefault();
       if(password === conPassword){
        registerWithEmail(email, password, name);
        setName('')
        setEmail('')
        setPassword('')
        setConpassword('')
       }else{
           alert('check your password')
       }
      
      
   }

    return (
        <div className='register'>
            <h2>Register here</h2>
            <form onSubmit={handleSubmit}>
            <div className="form-floating mb-3">
  <input type="text" className="form-control"  placeholder="Enter Your Name" onChange={e=> setName(e.target.value)} value={name}/>
  <label for="floatingInput">User Name</label>
</div>
            <div className="form-floating mb-3">
  <input type="email" className="form-control"  placeholder="name@example.com" onChange={e=> setEmail(e.target.value)} value={email}/>
  <label for="floatingInput">Email address</label>
</div>
<div className="form-floating">
  <input type="password" className="form-control mb-3"  placeholder="Password" onChange={e=> setPassword(e.target.value)} value={password}/>
  <label for="floatingPassword">Password</label>
</div>
<div className="form-floating">
  <input type="password" className="form-control mb-3"  placeholder="Confirm Password" onChange={e=> setConpassword(e.target.value)} value={conPassword}/>
  <label for="floatingPassword">Conform Password</label>
</div>
<button className='btn  btn-outline-primary'>Submit</button>
            </form>
            <button className='btn  btn-outline-secondary mb-3 desk' onClick={googleSignIn}>Google Login</button>
            <button className='btn  btn-outline-secondary mb-3 mobile' onClick={googleRedirect}>Google  Login</button>
            
        </div>
    )
}

export default RegisterPage
